.downloadBtn {
  all: unset;
  background-color: #fff;
  color: white;
  cursor: pointer;
  margin-left: 10px;
  padding: 2px 4px 0;
  border-radius: 4px;
  transition: 0.7s;

  &:hover {
    background-color: #28e98c;
  }

  .downloadIcon {
    width: 16px;
    height: 16px;
  }
}

